import { AppComponent } from './componments/app/app.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppRoutingModule } from './/app-routing.module';
import { OutlineComponent } from './componments/outline/outline.component';
import { VsComponent } from './componments/vs/vs.component';
import { ThanksComponent } from './componments/thanks/thanks.component';
import { TeamComponent } from './componments/team/team.component';
import { PrizeComponent } from './componments/prize/prize.component';


@NgModule({
  declarations: [
    AppComponent,
    OutlineComponent,
    VsComponent,
    ThanksComponent,
    TeamComponent,
    PrizeComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



