import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-outline',
  templateUrl: './outline.component.html',
  styleUrls: ['./outline.component.css']
})
export class OutlineComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    localStorage.setItem('allName', JSON.stringify(this.all));
  }
  public a: string[] = ['坤', '只为等你', '包子', '汗汗', '粉猪', '木易'];
  public b: string[] = ['小小方', '浪人', '不懂', '阿振', '婷婷', '豆沙包'];
  public c: string[] = ['夜潇枫', '动G', '小玲', '成上', '花花', '瑞'];
  public d: string[] = ['刘威', '涛涛', 'Poppy', '浩子', '叶子', '阿荣'];
  public e: string[] = ['阿艺', '小犬', '关关', 'Rain', '朱古力', 'Sunny'];
  public all: any[] = [this.a, this.b, this.c, this.d, this.e];
  public teamNum: string[] = ['A', 'B', 'C', 'D', 'E'];

}



