import { Component, OnInit } from '@angular/core';
import { forEach } from '_@angular_router@5.1.3@@angular/router/src/utils/collection';

@Component({
  selector: 'app-team',
  templateUrl: './team.component.html',
  styleUrls: ['./team.component.css']
})
export class TeamComponent implements OnInit {
  all:any[]=[];
  allTeam:any[]=[];
  constructor() { }
  allName:any[]=JSON.parse(localStorage.getItem('allName'));
  ngOnInit() {
    if (localStorage.getItem('all')) {
        this.all = JSON.parse(localStorage.getItem('all'));
    }
    this.allName.forEach(everyTeamNamesArray => {
      let temp=[];
      everyTeamNamesArray.forEach(everyoneName => {
        this.all.forEach(changdi => {
            changdi.member.forEach(member => {
              if(member.name===everyoneName){
                  temp.push(member);
              }
            });
        });
      });
      this.allTeam.push({
        total:0,
        member:temp
      });
    });
    this.allTeam.forEach(team => {
        team.member.forEach(member => {
            team.total+=member.s1;
        });
    });
    this.allTeam.sort((a, b) => {
      if (a['total'] && b['total']) {
        if (a['total'] > b['total']) {
          return -1;
        }
        if (a['total'] < b['total']) {
          return  1;
        }
        // a 必须等于 b
        return 0;
      }
    });
  }
}



