import { AppComponent } from './componments/app/app.component';
import { OutlineComponent } from './componments/outline/outline.component';
import { Routes,RouterModule} from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { VsComponent } from './componments/vs/vs.component';
import { TeamComponent } from './componments/team/team.component'
import { PrizeComponent } from './componments/prize/prize.component'


const appRoutes: Routes = [
  {
    path: '',
    redirectTo: 'outline',
    pathMatch: 'full'
  },
  {
    path: 'outline',
    component: OutlineComponent,
    data: {title: 'Select Club'}
  },
  {
    path: 'vs',
    component: VsComponent,
    data: {title: 'Select Club'}
  },
  {
    path: 'team',
    component: TeamComponent,
    data: {title: 'Select Club'}
  },
  {
    path: 'prize',
    component: PrizeComponent,
    data: {title: 'Select Club'}
  }

];
@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }





